		-ms-transform: translateY(0);
		transform: translateY(0);
		visibility: visible;
		transition: all .3s ease
	}
	.notfications .dropdown-menu::before {
		right: 1px;
		margin-right: 0
	}
	.notification-custom-scroll {
		width: 100% !important;
		max-height: calc(100vh - 55px)
	}
	.notification-custom-scroll>div {
		margin-right: 0 !important
	}
	.notification-custom-scroll>div:first-child {
		padding-bottom: 20px
	}
	.action-column .select-mark {
		display: none
	}
	.action-column .activities-triger {
		margin-left: 0
	}
	.activities-triger .active {
		position: absolute;
		left: 0;
		top: -17px;
		border-radius: 2px;
		width: 3px;
		height: 52px;
		box-shadow: 0 0 4px 0 #46e1b6;
		background-color: #46e1b6;
		object-fit: contain
	}
	.name.col {
		width: 101px
	}
	.action-column {
		width: 12px !important
	}
	.campaign-chart>.row {
		padding-left: 0
	}
	.campaign-chart>.row::before {
		display: none
	}
}

@media only screen and (max-width: 1200px) {
	.filter-dropdown {
		left: auto !important;
		right: 0
	}
	.filter-dropdown.dropdown-box::before {
		left: auto !important;
		right: 15% !important
	}
}